.bad {
  /* width: 55px; */
  height: 26px;
  border-radius: 4px;
  border: 1px solid var(--ui-red, #ea1a4f);
  background: var(--ui-light-red, #fee9ef);
  color: var(--ui-red, #ea1a4f);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%;
  padding: 6px 8px;
  text-align: center;
}

.good {
  text-align: center;
